.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.mainaboutdiv {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 80%;
  margin: auto;
  margin-top: 3rem;
  justify-content: center;
  /* border: 2px solid red; */
}

/* about image styleing */

.aboutbutton {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
.aboutbutton button {
  font-size: 15px;
  font-weight: bold;
}

/* Space image of the dool  */

.spacetraveller {
  z-index: -3;
}
.spacetraveller img {
  margin-top: -5rem;
}

@keyframes updown {
  0% {
    transform: translateY(-50px);
  }

  50% {
    transform: translateY(30px);
  }

  100% {
    transform: translateY(-50px);
  }
}
.txt-rotate > .wrap {
  border-right: 0.08em solid #666;
}

.aligh-items-center {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: auto;
}

/* End image spcase herel  */

.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about_card {
  background: rgb(237 242 248);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about_card h3,
h4 {
  color: black;
}

.about_card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about_icon {
  color: black;
  font-size: 1.9rem;
  margin-bottom: 1rem;
}

.about_card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about_content ul {
  margin: 2rem 0 2.6rem;
  color: black;
  text-align: left;
}
.about_content ul li {
  margin: 10px;
  color: black;
  text-align: left;
}

.about_content {
  text-align: center;
}

/* RESPONSIVE DESIGN OF THE ABOUT PAGE  */

@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about_content p {
    margin: 1rem 0 1.5rem;
    text-align: center;
  }
  .about_content {
    text-align: center;
  }
  .mainaboutdiv {
    grid-template-columns: repeat(1, 1fr);
  }
  .aligh-items-center {
    width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about_content {
    text-align: center;
  }
  .about_content p {
    margin: 1.5rem 0;
    text-align: center;
  }
}
