/* ####################   CTA    ############# */

.cta {
  padding-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
}

/* #############  HEADER SOCIAL ############## */

.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
  z-index: 1;
  position: fixed;
  left: 0;
}

/* ################### MAIN CSS OF THE DIV ################## */

.banner {
  padding-bottom: 11rem;
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.811),
    rgb(237 242 248)
  );
}

.maincenterdiv {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: auto;
  align-items: center;
  color: black;
  height: 100%;
  padding-bottom: 3rem;
  padding-top: 9rem;
}

.Myphoto img {
  border-radius: 1.5rem;
  border-top-left-radius: 15rem;
  border-top-right-radius: 15rem;
  width: 75%;
  margin: auto;
  transition: width 0.3s ease-in-out;
  pointer-events: none;
}

.Alldetails h3 {
  font-weight: bolder;
  font-size: 2rem;
}

.Alldetails h1 {
  font-weight: 900;
  font-size: 4rem;
}

.Alldetails h2 {
  font-weight: bolder;
  font-size: 2rem;
}

/* #################  media query of the website  ############### */

@media screen and (max-width: 1100px) {
  .maincenterdiv {
    width: 100%;
    justify-content: center;
  }
  .Myphoto img {
    width: 60%;
  }

  .aligh-items-center {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .banner {
    padding-bottom: 5rem;
  }
}

@media screen and (max-width: 950px) {
  .maincenterdiv {
    display: block;
    width: 100%;
    text-align: center;
  }
  .cta {
    display: block;
    padding: 20px;
  }
  .cta a {
    margin: 1rem;
  }
  .Myphoto img {
    width: 40%;
  }

  .aligh-items-center {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .banner {
    padding-bottom: 2rem;
  }
}

@media screen and (max-width: 800px) {
  .header_socials {
    display: none;
  }
}

@media screen and (max-width: 700px) {
  .banner {
    padding-top: 30px;
  }
  .Myphoto img {
    width: 55%;
  }

  .header_socials {
    display: none;
  }
}
