#project {
  padding-bottom: 3rem;
  background-image: linear-gradient(
    to bottom right,
    rgb(255, 255, 255),
    rgb(237 242 248)
  );
}

.portfolio_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
}

.portfolio_item {
  background: var(--color-bg-variant);
  padding: 1.3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.portfolio_item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
}

/* button design part of the website */

.category_button {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 2rem;
}

.category_button button {
  color: black;
  font-weight: 700;
  letter-spacing: 1.5px;
  box-shadow: 0px 1px 6px -3px black;
  border: 1px solid aliceblue;
}

.Headline_Project {
  color: black;
  font-weight: 900;
}

.portfolio_item_image {
  border-radius: 10px;
  overflow: hidden;
}

.portfolio_item_image img {
  width: 100%;
  height: 13rem;
}

.portfolio_item_image:hover {
  -webkit-box-shadow: -1px -3px 47px -15px rgba(0, 0, 0, 1);
  -moz-box-shadow: -1px -3px 47px -15px rgba(0, 0, 0, 1);
  box-shadow: -1px -3px 47px -15px rgba(0, 0, 0, 1);
}

.portfolio_item h3 {
  font-weight: bold;
  margin: 5px 2rem;
  text-align: center;
  letter-spacing: 1px;
}

.portfolio_item p {
  text-align: left;
  margin: 15px 0px;
}

.portfolio_item_cta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  justify-content: center;
}

/* MEDIA QUERY OF THE WEBSITE  */

@media screen and (max-width: 1024px) {
  .portfolio_container {
    grid-template-columns: 1fr 1fr;
    gap: 1.4rem;
  }
}

@media screen and (max-width: 600px) {
  .portfolio_container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .category_button {
    gap: 1rem;
  }
}
